"use client";

import * as Plot from "@observablehq/plot";
import { useEffect, useRef } from "react";
import { partyColors } from "@/lib/config/colors";

interface HouseEffect {
  pollster: string;
  party: string;
  effect: number;
  n_polls: number;
}

interface HouseEffectsProps {
  data: HouseEffect[];
  height?: number;
}

export function HouseEffects({ data, height = 300 }: HouseEffectsProps) {
  const containerRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (!data || data.length === 0) return;

    // Filter for pollsters with at least 3 polls and meaningful effects
    const filteredData = data.filter(d => 
      d.n_polls >= 3 && Math.abs(d.effect) > 0.005
    );

    const plot = Plot.plot({
      height,
      marginLeft: 120,
      marginRight: 80,
      marginTop: 20,
      marginBottom: 40,
      style: {
        backgroundColor: "transparent",
        fontSize: "11px",
        fontFamily: "Inter, system-ui, sans-serif"
      },
      x: {
        label: "House effect (percentage points)",
        grid: true,
        tickFormat: d => `${(d * 100).toFixed(1)}%`,
        domain: [-0.08, 0.08]
      },
      y: {
        label: null,
        domain: [...new Set(filteredData.map(d => d.pollster))].sort()
      },
      color: {
        type: "categorical",
        domain: Object.keys(partyColors),
        range: Object.values(partyColors)
      },
      marks: [
        // Background grid
        Plot.gridX({ stroke: "#f3f4f6", strokeWidth: 1 }),
        Plot.ruleX([0], { stroke: "#6b7280", strokeWidth: 2 }),
        
        // House effect bars
        Plot.barX(filteredData, {
          x: "effect",
          y: "pollster",
          fill: "party",
          fillOpacity: 0.8,
          height: 15,
          rx: 2
        }),
        
        // Value labels for significant effects
        Plot.text(filteredData.filter(d => Math.abs(d.effect) > 0.02), {
          x: "effect",
          y: "pollster",
          text: d => `${(d.effect * 100).toFixed(1)}%`,
          dx: d => d.effect > 0 ? 5 : -5,
          textAnchor: d => d.effect > 0 ? "start" : "end",
          fontSize: 10,
          fontWeight: "500",
          fill: "#374151"
        })
      ]
    });

    if (containerRef.current) {
      containerRef.current.replaceChildren(plot);
    }

    return () => plot.remove();
  }, [data, height]);

  if (!data || data.length === 0) {
    return (
      <div className="text-center py-8 text-gray-500">
        <p>House effects analysis loading...</p>
      </div>
    );
  }

  return (
    <div className="w-full">
      <div ref={containerRef} />
      <div className="mt-2 text-xs text-gray-500">
        Positive values indicate the pollster tends to show higher support for that party than the average.
        Only pollsters with 3+ polls and meaningful effects (&gt;0.5pt) are shown.
      </div>
    </div>
  );
}